@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.custom-navbar a {
    width: 120px;
    font-size: large;
    font-weight: bold;
    color: cadetblue !important;
    box-shadow: 2px 1px 1px grey;
}

.custom-navbar a:hover {
    font-size: large;
    font-weight: bold;
    font-family: 'Architects Daughter';
    background-color: whitesmoke;
    color: #233e8b !important;
    box-shadow: inset 5px 3px 3px grey;
}

.custom-navbar a.active {
    font-size: large;
    font-weight: bold;
    font-family: 'Architects Daughter';
    background-color: whitesmoke;
    color: #233e8b !important;
    box-shadow: inset 5px 3px 3px grey;
}

.home {
    height: 100vh;
    position: relative;
}

@media(max-width: 768px) {
    .home {
        height: 50vh;
        background-image: url(/david-henry-portfolio/static/media/wall-only.d3db2a03.jpg);
        background-size: 180%;
    }
}

.home::after {
    content:'';
    background-image: url(/david-henry-portfolio/static/media/wall-off-center-portrait.31984d6f.JPG);
    background-size: cover;
    position: absolute;
    top:0px;
    left: 0px;
    width:100%;
    height:100%;
    z-index:-1;
    opacity: 0.9;
}

.mobile-portrait {
    height: 100vh;
    position: relative;
}

.mobile-portrait::after {
    content:'';
    background-image: url(/david-henry-portfolio/static/media/wall-off-center-portrait-cropped.20a03be3.jpg);
    background-size: cover;
    position: absolute;
    top: 0px;
    left: 0px;
    width:100%;
    height:100%;
    z-index:-1;
    opacity: 0.9;
}

.about {
    text-align: left;
}

.about-title {
    text-align: center;
    height: 40vh;
    color: white;
    background-color: #ffffff;
    background-image: linear-gradient(35deg, lightseagreen, #233e8b);
}

.about p {
    text-indent: 50px;
}

.about-images {
    width: 100%;
    height: 30vh;
    object-fit: cover;
    border-radius: 10px;
}

.code-backdrop-img {
    object-position: top;
    opacity: 1;
    transition: 1s ease-in-out;
}

.code-backdrop-img:hover {
    opacity: 0.1;
    object-position: bottom;
}

.html-cover-img {
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: .5s ease-in-out;
}

.html-cover-img:hover {
    cursor: url(/david-henry-portfolio/static/media/dragon-cursor.aacecc08.png), auto;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.acu-banner-img {
    -webkit-filter: blur(1px);
            filter: blur(1px);
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    transition: 1s ease-in-out;
}

.acu-banner-img:hover {
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
}

.platform-images {
    background-image: url(/david-henry-portfolio/static/media/khan-academy.b0e86626.png);
    background-size: cover;
    background-position: center;
    transition: 1s ease-in-out;
}

.platform-images:hover {
    background-image: url(/david-henry-portfolio/static/media/code-org.f93e2e75.png);
    background-size: cover;
    background-position-y: 45%;
}

.teacher-certificate-img {
    object-fit: contain;
    border: 2px solid grey;
    background-color: #fff;
    -webkit-filter: grayscale(0) blur(0);
            filter: grayscale(0) blur(0);
    transition: .3s ease-in-out;
}

.teacher-certificate-img:hover {
    -webkit-filter: grayscale(100%) blur(2px);
            filter: grayscale(100%) blur(2px);
}

.edu-schedule-img {
    object-position: top;
    box-shadow: 5px 5px 5px grey;
    transition: 1s ease-in-out;
}

.edu-schedule-img:hover {
    box-shadow: 30px 30px 3px lightseagreen;
    -webkit-transform: translate(-10px, -10px);
            transform: translate(-10px, -10px);
}

.react-banner-img {
    -webkit-filter: invert(0%);
            filter: invert(0%);
    transition: 2s ease-in-out;
}

.react-banner-img:hover {
    -webkit-filter: invert(100%);
            filter: invert(100%);
}

.nucamp-img {
    border: 2px solid grey;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    transition: .5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.nucamp-img:hover {
    -webkit-transform: rotate(6deg);
            transform: rotate(6deg);
}

.skills-title {
    text-align: center;
    height: 40vh;
    margin-bottom: 50px;
    color: white;
    background-color: #ffffff;
    background-image: linear-gradient(35deg, lightseagreen, #233e8b);
}

.list-title-corner {
    width: 20%;
    border-left: none !important;
    border-top: none !important;
    border-bottom: none !important;
}

.list-title-item {
    width: 20%;
}

.list-header-item {
    width: 20%;
    color: white;
    background-image: linear-gradient(35deg, lightseagreen, #233e8b);
    font-weight: bold;
}

.list-content-title {
    width: 20%;
}

.list-content-progress {
    width: 80%;
}

@media (max-width: 768px) {
    .list-title-corner {
        display: none !important;
    }
    .list-title-item {
        display: none !important;
    }
    .list-header-item {
        width: 40%;
    }
    .list-content-title {
        width: 40%;
        font-size: small;
    }
}

.progress-bar {
    color: black !important;
    font-weight: bold;
}

.progress .bg-css {
    background-color: deepskyblue;
}

.progress .bg-sass {
    background-color: mediumorchid;
}

.progress .bg-bootstrap {
    background-color: mediumpurple;
}

.progress .bg-javascript {
    background-color: darkorange;
}

.progress .bg-jquery {
    background-color: rgb(65, 152, 187);
}

.progress .bg-node {
    background-color: darkgreen;
}

.progress .bg-react {
    background-color: rgb(86, 170, 255);
}
.progress .bg-redux {
    background-color: rgb(118, 74, 188)
}

.progress .bg-sql {
    background-color: limegreen;
}

.progress .bg-node {
    background-color: grey;
}

.progress .bg-mongoose {
    background-color: rgb(165, 29, 29);
}

.carousel-images {
    height: 40vh;
    width: 100%;
}

.projects-title {
    text-align: center;
    height: 40vh;
    margin-bottom: 50px;
    color: white;
    background-color: #ffffff;
    background-image: linear-gradient(35deg, lightseagreen, #233e8b);
}

.projects-header {
    text-align: left;
    padding: 10px;
    background-color: rgb(238, 235, 235);
    border-left: 2px solid #233e8b;
}

.projects-link, .projects-link:hover {
    text-decoration: none;
    color: black !important;
}

.projects-images {
    height: 30vh;

}

.projects-card {
    box-shadow: none;
    color: inherit;
    transition: 0.2s ease-in-out;
}

.projects-card:hover {
    -webkit-filter: opacity(0.7);
            filter: opacity(0.7);
    box-shadow: 5px 5px 5px grey;
}

.resume {
    height: 100vh;
    background-color: cadetblue;
}

.contact {
    height: 100vh;
    background-color: cadetblue;
}

.footer {
    height: 20vh;
    color: white;
    background-color: #233e8b;
}

.footer a {
    color: white;
}

.social-icons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.App {
  text-align: center;
}

.main-content {
  height: 90vh;
  width: 100%;
  overflow: scroll;
}

